import React, { Component } from 'react';
import './App.css';
import Movie from './Movie';
import Select from 'react-select';

// render : componentWillMount() -> render() -> componentDidMount()
// update : componentWillReceiveProps() -> ShouldComponentUpdate() ( == true) -> componentWillUpdate() -> Render() -> componentDidUpdate()
const options = [
  {value:"download_count", label:"download count"},
  {value:"rating", label:"rating"},
  {value:"year", label:"year"},
];

class App extends Component {
  state = {
    selectValue:"rating"
  }
  componentDidMount(){
    this._getMovies();
  }

  _getMovies = async() => {
    const movie = await this._callApi();
    this.setState({
      movies : movie

    })
  }

  _callApi = () => {
    return fetch('https://yts.am/api/v2/list_movies.json?sort_by=' + this.state.selectValue)
    .then(fetch_res => fetch_res.json())
    .then(json => json.data.movies)
    .catch(err => console.log(err))
  }

  _updateValue = (newValue) => {
    this.setState({
      selectValue: newValue,
    });
    this._getMovies();
  }

  _renderButton = () => {
    return (
      <div>
      <Select
      id="order_by"
      ref={(ref) => { this.select = ref; }}
      onBlurResetsInput={false}
      onSelectResetsInput={false}
      autoFocus
      options={options}
      simpleValue
      clearable={false}
      name="order_by"
      disabled={false}
      value={this.state.selectValue}
      onChange={this._updateValue}
      rtl={false}
      searchable={false}
      />

      </div>
    );
  }
  _renderMovies = () => {
    const movies = this.state.movies.map((e/*, idx : 맵의 인덱스틑 느리므로 쓰지말자*/) => {
      // key 안 넣으면 warning
      //console.log(e);
      return <
      Movie key={e.id}
      title={e.title_english}
      poster={e.medium_cover_image}
      genres={e.genres}
      synopsis={e.synopsis}
      />
    })
    return movies
  }
  render() {
    //console.log('render');
    const {movies} = this.state;
    return (
      <div className={movies ? "App" : "App_loading"}>
      {movies ? this._renderButton() : ''}
      {movies ? this._renderMovies() : 'Loading'}
      </div>
    );
  }
}

export default App;



