import React  from 'react';
import PropTypes from 'prop-types';
import LinesEllipsis from 'react-lines-ellipsis';
import './Movie.css';

// class Movie extends Component{
//   // 안써도 되지만 안쓰면 암묵적타입, 쓰면 명시적 타입이 됨
//   static propTypes = {
