//     poster: PropTypes.string.isRequired
//   }
//   render(){
//     return (
//       <div>
//         <MoviePoster poster={this.props.poster}/>
//         <h1>{this.props.title}</h1>
//       </div>
//     )
//   }
// }
function Movie({title, poster, genres, synopsis}){
  return(
    <div className="Movie">
      <div className="Movie_colums">
        <MoviePoster poster={poster} alt={title}/>
      </div>
      <div className="Movie_colums">
        <h1>{title}</h1>
        <div className="Movie_genres">
          {genres.map((in_genre, index) => <MovieGenre genre={in_genre} key={index}/>)}
        </div>
        <LinesEllipsis className="Movie_Synopsys"
          text={synopsis}
          maxLine='3'
          ellipsis='...'
          trimRight
          basedOn='letters'
        />
      </div>
    </div>
  )
}

// class MoviePoster extends Component{
//   static propTypes = {
//     poster: PropTypes.string.isRequired
//   }
//   render(){
//     return(
//       // alt 안 넣으면 warning. 이미지 없을 때 출력할 텍스트
//       <img alt="test" src={this.props.poster}/>
//     )
//   }
// }
function MoviePoster ({poster, alt}){
  return (
    <img alt={alt} src={poster} title={alt} className="Movie_Poster"/>
  )
}

function MovieGenre({genre}){
  return (
    <span className="Movie_Genre">{genre} </span>
  )
}

MovieGenre.propTypes = {
  genre: PropTypes.string.isRequired
}
MoviePoster.propTypes = {
  poster: PropTypes.string.isRequired
}
Movie.propTypes = {
  title: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  genres: PropTypes.array.isRequired,
  synopsis: PropTypes.string.isRequired,

}
export default Movie;



